import BasicTitle from "@/Components/BasicTitle/BasicTitle";
import Image from "next/image";
import DefaultImage from '@/public/images/Default-Image.png'
import MembersCardList from "@/Components/MembersCardList/MembersCardList";
import PlayerCard from "@/Components/MembersCardList/PlayerCard/PlayerCard";
import PersonCard from "@/Components/MembersCardList/PersonCard/PersonCard";

export default function CoachPage() {
    let idCoach = 1;
    const data = new Array(4).fill(null).map((value, index) => {
        const infoPlayer = { id: idCoach, name: `Имя`, surname: "Фамилия", post: "Должность" };
        idCoach++;
        return (infoPlayer)
    })


    return (
        <div className="w-full flex flex-col justify-center items-center">
            <div className="max-w-[1200px] text-center">
                <BasicTitle text="Состав команды" />
                <div className="w-full">
                    <Image className="w-full rounded-[10px]" alt="Состав команды Зенит Ижевск" src={DefaultImage} />
                </div>
                {data.map((persons) => {
                    return (
                        <div key={`Список ${persons.role}`} className="w-full pt-[30px] pb-[0px]">
                            <MembersCardList title={persons.role} >
                                {persons.info.map((person) => {
                                    return (
                                        <PersonCard
                                            key={person.name + ' ' + person.surname + ' ' + '-idPlayer=' + person.id}
                                            id={person.id}
                                            name={person.name}
                                            surname={person.surname}
                                            post={person.post}
                                        />
                                    )
                                })}
                            </MembersCardList>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}