import './style.css';
export default function GradientHover() {

    return (
        <>
            <div className="absolute transition-all opacity-100 duration-300 inset-0 h-full z-20 group-hover:opacity-0 pointer-events-none gradientHoverPassive" />
            <div className="absolute transition-all opacity-0 duration-300 inset-0 h-full z-20 group-hover:opacity-100 pointer-events-none gradientHoverActive" />
        </>
    )
}